/*
button.scss
classes:
  - disabled
  - outlined
  - rounded
  - block
  - large
  - text
  - flat
*/
.btn {
  padding: 0;
  margin: 0;
  border: none;
  font: inherit;
  color: inherit;
  cursor: pointer;
  outline: none;
  position: relative;
  text-decoration: none;
  background-color: transparent;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-height: 2.5rem;
  min-width: 4rem;
  padding: 0 1rem;
  margin: 0 2px 1rem;
  color: white;
  border: solid 1px transparent;
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.85);
  font-weight: 400;
  border-radius: 2px;
  letter-spacing: 1px;
  text-transform: capitalize;
  background-color: #1a237e;
  transition: all 0.2s; }
  .btn.disabled {
    opacity: 0.4;
    box-shadow: none !important;
    cursor: default; }
    .btn.disabled:hover {
      opacity: 0.4;
      box-shadow: none !important; }
      .btn.disabled:hover:active {
        opacity: 0.4; }
  .btn:hover {
    opacity: 0.8;
    box-shadow: 0 3px 5px 0px rgba(0, 0, 0, 0.85); }
    .btn:hover:active {
      opacity: 0.6; }
  .btn.flat {
    box-shadow: none !important; }
  .btn.outlined {
    box-shadow: none !important;
    color: #1a237e;
    border-color: #1a237e;
    background-color: rgba(26, 35, 126, 0); }
    .btn.outlined:hover {
      background-color: rgba(26, 35, 126, 0.2); }
      .btn.outlined:hover:active {
        background-color: rgba(26, 35, 126, 0.15); }
    .btn.outlined.disabled {
      background-color: transparent !important; }
  .btn.text {
    box-shadow: none !important;
    padding: 0 calc(1rem / 2);
    border: transparent !important;
    color: #1a237e;
    background-color: rgba(26, 35, 126, 0); }
    .btn.text:hover {
      background-color: rgba(26, 35, 126, 0.2); }
      .btn.text:hover:active {
        background-color: rgba(26, 35, 126, 0.15); }
    .btn.text.disabled {
      background-color: transparent !important; }
    .btn.text.large {
      padding: 0 1rem; }
  .btn.block {
    width: 100%; }
  .btn.rounded {
    border-radius: calc(2.5rem); }
  .btn.large {
    padding: 0 calc(1rem * 2);
    min-height: calc(2.5rem * 1.25); }

a {
  text-decoration: none;
  color: #1a237e;
  display: inline-flex;
  align-items: center;
  transition: color 0.2s;
  position: relative; }
  a:hover {
    color: #0d113f; }
  a.underline::after {
    content: "";
    border-bottom: solid 3px #0d113f;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    transform: scaleX(0);
    transform-origin: right;
    transition: transform 0.25s; }
  a.underline:hover::after {
    transform-origin: left;
    transform: scaleX(1); }

@keyframes underline {
  from {
    transform: scaleX(0); }
  to {
    transform: scaleX(1); } }

